export default {
  props:["productCate"],
  data() {
    return {
      optionValues:{}
    };
  },
    mounted(){
    for(let cate in this.productCate) {
      for(let product in cate.products) {
        const key = cate.name + '-' + product.name
        this.optionValues[key] = false
      }
    }
    
